export default {
	install(Vue) { // installmixin
		Vue.mixin({
			data() {
				return {
					userrules: {
						name: {
							rules: [{
									required: true,
									'errorMessage': ""
								},
								{
									validateFunction: this.validateLoginName
								} // 
							]
						},
						pwd: {
							rules: [{
								required: true,
								'errorMessage': ""
							}, ]
						},
						mobile: {
							rules: [{
									required: true,
									'errorMessage': ""
								},
								{
									validateFunction: this.validatePhone
								} // 
							]
						},
						passcode: {
							rules: [{
									required: true,
									"errorMessage": ""
								},
								{
									validateFunction: this.validateCode
								}
							]
						}
					},
					mobileReg: /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/
				}
			},
			methods: {
				validateCode(rule, val, data, callback){
					switch (true) {
						case val !== this.passCode:
							callback('')
							break;
						default:
							return true
					}
				},
				validateLoginName(rule, val, data, callback) {
					switch (true) {
						case val.length < 6:
							callback('')
							break;
						default:
							return true
					}
				},
				/*  */
				validatePhone(rule, val, data, callback) {
					switch (true) {
						case !this.mobileReg.test(val):
							callback('')
							break;
						default:
							return true
					}
				}
			}
		})
	}
}
